<template>
  <div>
    <input-form label="商品名" v-model="items.name"></input-form>
    <input-form label="消費期限" v-model="items.expiryDate"></input-form>
    <input-form label="購入者" v-model="items.purchaser"></input-form>
    <input-form label="個数" v-model="items.count"></input-form>
    <input-form label="備考" v-model="items.description"></input-form>
    <router-link to="search">中身をみる</router-link>
    <router-link to="search" @click.native="updateFood(items,priKey)">編集完了</router-link>
    <change-list text="変更前" :item="$route.params.item"></change-list>
    <change-list text="変更後" :item="items"></change-list>
  </div>
</template>

<script>
import firebase from "firebase";
import ChangeList from "../components/changeList.vue";
import InputForm from "../components/InputForm.vue";

export default {
  name: "editForm",
  data() {
    return {
      database: null,
      fridgeRef: null,
      items: {
        name: "",
        expiryDate: "",
        count: "",
        purchaser: "",
        description: ""
      },
      priKey: "",
      fridge: [],
      fridgeName: ""
    };
  },
  created: function() {
    this.fridgeName = localStorage.getItem("fridge");
    this.database = firebase.database();
    this.fridgeRef = this.database.ref(this.fridgeName);

    let _this = this;
    this.fridgeRef.on("value", function(snapshot) {
      _this.fridge = snapshot.val();
    });
  },
  components: {
    ChangeList,
    InputForm
  },
  mounted: function() {
    this.items.name = this.$route.params.item.name;
    this.items.expiryDate = this.$route.params.item.expiryDate;
    this.items.count = this.$route.params.item.count;
    this.items.purchaser = this.$route.params.item.purchaser;
    this.items.description = this.$route.params.item.description;
    this.priKey = this.$route.params.primaryKey;
  },
  props: {
    item: Object
  },
  methods: {
    updateFood: function(item, key) {
      let updates = {};
      updates["/" + this.fridgeName + "/" + key] = item;
      this.database.ref().update(updates);
    }
  }
};
</script>

