<template>
  <div id="form">
    <div v-for="(item,key) in fridge" :key="key">
      <item-obj :item="item"></item-obj>
      <router-link to="/search" @click.native="deleteFood(key)">削除</router-link>
      <router-link :to="{name:'change',params:{item:item,primaryKey:key}}">編集</router-link>
    </div>
    <router-link to="send">追加に戻る</router-link>
  </div>
</template>


<script>
import firebase from "firebase";
import ItemObj from "../components/itemObj.vue";

export default {
  name: "outputData",
  created: function() {
    this.fridgeName = localStorage.getItem("fridge");
    this.database = firebase.database();
    this.fridgeRef = this.database.ref(this.fridgeName);
  },
  methods: {
    deleteFood: function(key) {
      this.database
        .ref(this.fridgeName)
        .child(key)
        .remove();
    }
  },
  data() {
    return {
      database: null,
      fridgeRef: null,
      fridgeName: ""
    };
  },
  props: {
    fridge: Object
  },
  components: {
    ItemObj
  }
};
</script>

