import Vue from "vue";
import Router from "vue-router";

import sendData from "./views/sendData.vue";
import change from "./views/editForm.vue";
import output from "./views/outputData.vue";
import home from "./views/fridgeHome.vue";
import search from "./views/searchForm.vue";

Vue.use(Router);

export default new Router({
  mode: "history",
  routes: [
    {
      path: "/send",
      name: "send",
      component: sendData
    },
    {
      path: "/output",
      name: "output",
      component: output
    },
    {
      path: "/change",
      name: "change",
      component: change
    },
    {
      path: "/",
      name: "home",
      component: home
    },
    {
      path: "/search",
      name: "search",
      component: search
    }
  ]
});
